import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import FormFileView from "../../../Ui/Input/FormFileView";

const RndProjectHeadDetailCard = ({ rnd }) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {t("title")} {": "}
                {rnd?.title ?? ""}
              </h6>
              <p>
                {t("sr_no_only")} {": "}
                {rnd?.srn ?? ""}
              </p>
              <p>
                {t("student")} {": "}
                {`${rnd?.student?.studentFirstName ?? ""} ${
                  rnd?.student?.studentMiddleName ?? ""
                } ${rnd?.student?.studentLastName ?? ""}`}
              </p>
              <p>
                {t("class")} {": "}
                {`${rnd?.classes?.className ?? ""} -  ${
                  rnd?.classes?.classTitle ?? ""
                }`}
              </p>
              <p>
                {t("subject_only_1")} {": "}
                {rnd?.subject ?? ""}
              </p>
              <p>
                {t("guide_name")} {": "}
                {rnd?.guide_name ?? ""}
              </p>

              {rnd?.link ? (
                <p>
                  {t("link")} {": "}
                  <a
                    href={rnd?.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{
                      color: "#0942a6",
                    }}
                  >
                    {rnd?.link}
                  </a>
                </p>
              ) : null}
              {rnd?.attach ? (
                <FormFileView
                  label={`${t("attachment")} :`}
                  value={{
                    key: rnd?.attach,
                  }}
                  customFormInputContainer={{
                    marginBlock: "0",
                  }}
                />
              ) : null}

              <p
                style={{
                  whiteSpace: "break-spaces",
                }}
              >
                {t("abstract")} {": "}
                {rnd?.abstract ?? ""}
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default RndProjectHeadDetailCard;

