import { useTranslation } from "react-i18next";
import style from "../../StoreManager/StoreManager.module.css";
import FormFileView from "../../../Ui/Input/FormFileView";

const RndActivitiesHeadDetailCard = ({ rnd }) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {t("activity_name")} {": "}
                {rnd?.activity_name ?? ""}
              </h6>
              <p>
                {t("activity_type")} {": "}
                {rnd?.activity_type ?? ""}
              </p>
              <p>
                {t("universal_batch")} {": "}
                {rnd?.activity_batch?.batchName ?? ""}
              </p>
              <p>
                {t("staff")}
                {": "}
                {`${rnd?.activity_staff?.staffFirstName ?? ""} ${
                  rnd?.activity_staff?.staffMiddleName ?? ""
                } ${rnd?.activity_staff?.staffLastName ?? ""}`}
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default RndActivitiesHeadDetailCard;

